.registration__frame {
  display: block;
  margin: 0;
  min-width: 510px;
  min-height: 290px;
  padding: 15px 12px 34px 12px;
  margin-left: auto;
  margin-right: auto;
  background: #fffff9;
  box-shadow: -4px -4px 4px rgba(171, 129, 108, 0.15), 4px 4px 4px rgba(171, 129, 108, 0.15);
  border-radius: 150px 0px 0px 150px;
  transform: rotate(90deg);

  @media screen and (min-width: 480px) {
    width: 290px;
    height: 510px;
  }
  @media screen and (min-width: 656px) {
    display: flex;
    width: 634px;
    height: 260px;
    background: #fffff9;
    box-shadow: -4px -4px 4px rgba(171, 129, 108, 0.15), 4px 4px 4px rgba(171, 129, 108, 0.15);
    border-radius: 150px;
    transform: rotate(0);
  }
}

