.registration-text__one,
.registration-text__two {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  color: var(--main-text-color);
}
.registration-text__one {
  max-width: 118px;
  margin: 0 auto;
  margin-top: 0;
  margin-bottom: 24px;
}
.registration-text__two {
  margin-top: 0;
  margin-bottom: 22px;
}
.registration-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 25px;
  margin-top: 0;

  &::before {
    content: '';
    width: 100%;
    margin-bottom: 10px;
    border: 1px solid rgba(171, 129, 108, 0.5);
  }
}
.registration-form__join {
  max-width: 220px;
  margin: 0 auto;
  margin-top: 0;
  margin-bottom: 17px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: var(--main-text-color);
}

.registration-form__input {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  width: 100%;
  margin-top: 0;
  padding-bottom: 2px;
  background-color: transparent;
  border-left: none;
  border-right: none;
  border-top: none;
  border-bottom: 1px solid #ab816c;
  &:not(:last-child) {
    margin-bottom: 25px;
  }
}

.registration-form__input:-webkit-autofill,
.registration-form__input:-webkit-autofill:hover,
.registration-form__input:-webkit-autofill:focus {
  -webkit-text-fill-color: currentColor;
}
.registration-form__input:focus {
  outline: none;
  border-bottom: 1px solid var(--accent-color);
}
.registration-form__input:focus::placeholder {
  color: var(--accent-color);
}
.registration-form__input::placeholder {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  color: rgba(171, 129, 108, 0.5);
}
.btn {
  margin-right: auto;
  margin-left: auto;
}
